
@import '@craftcms/sass/mixins';

.info-hud {
  @apply tw-inline-block;

  .v-popover {
    @apply tw-text-gray-400;
    display: inline-block;
    line-height: 0;

    &:hover,
    &.open {
      @apply tw-cursor-pointer tw-text-blue-600;
    }

    & > span {
      outline: none;
    }
  }
}

.tooltip {
  max-width: 250px;
}
