
@import '@craftcms/sass/mixins';

/* Category Selector Btn */

.category-selector-btn {
  @apply tw-block tw-relative;
  @apply lg:tw-hidden;
  background: $grey050;
  border: 1px solid $hairlineColor;
  padding: 10px 20px;
  border-radius: 4px;
  color: $secondaryColor;

  &:before {
    @include icon;
    @apply tw-absolute tw-right-0;
    top: calc(50% - 10px);
    font-size: 16px;
    width: 43px;
    line-height: 20px;
    content: 'downangle';
  }

  &:hover {
    @apply tw-no-underline;
  }
}

/* Category Selector */

.category-selector {
  @apply lg:tw-flex tw-flex-col tw-fixed tw-top-0 tw-left-0 tw-bg-white tw-z-20;
  @apply tw-hidden;
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;

  .category-selector-body {
    @apply tw-overflow-auto tw-h-full tw-p-6;
    box-sizing: border-box;

    .nav-items {
      ul {
        li {
          &:first-child a {
            border-top: 0;
          }

          &:first-child:before {
            @apply tw-hidden;
          }

          &:before,
          &:after {
            left: 1rem;
            right: 1rem;
          }

          a {
            padding-left: 55px;

            img {
              left: 24px;
            }
          }
        }
      }
    }
  }
}
